import React, { Component } from 'react'
import ImagePreloader from '../ImagePreloader/ImagePreloader';

export default class Knowledge extends Component {
    render() {
        return (
            <div>
                <div className="uk-card uk-card-default uk-card-body uk-border-rounded ">
                    <div className="uk-card-body">
                        {
                            this.props.preferido ? <div className="uk-card-badge uk-label">Preferido</div> : null
                        }
                        <h3 className={this.props.preferido ? "uk-card-title uk-margin-small-top" : "uk-card-title"}>{this.props.title}</h3>
                        <div className="uk-card-media">
                            <ImagePreloader 
                                src={this.props.image}
                                alt=""
                                width="115"
                                style={{height: "115px"}}
                            />
                        </div>
                        <progress className="uk-progress uk-margin-top" value={this.props.progress} max="100"></progress>
                    </div>
                </div>
            </div>
        )
    }
}
