import React, { Component, Fragment } from 'react'

export default class ImagePreloader extends Component {
    state = {
        src: this.props.src,
        loaded: false
    }

    onImageLoaded = () => {
        this.setState({loaded: true});
    }

    render() {
        const {src, loaded} = this.state;
        
        return (
            <Fragment>
                <img src={src} onLoad={this.onImageLoaded} 
                    className={ !loaded ? 'no-display' : this.props.className} 
                    width={this.props.width} 
                    height={this.props.height}
                    style={this.props.style}
                />
                { !loaded && <span data-uk-spinner={this.props.size ? this.props.size : 'ratio: 4;'} className="custom-color"></span>}
            </Fragment>
        )
    }
}
